<template>
  <div>
    <Loading v-if="isLoading"></Loading>
    <div class="main-quiz" v-else>
      <div class="progress">
        <div class="progress-bar">
          <div
            class="progress-percent"
            :style="{
              width: `${Math.round(
                100 * ((current + 1) / (this.end - this.begin + 1))
              )}%`,
            }"
          ></div>
        </div>
      </div>
      <div class="select-answer" :class="isResult ? 'prevent-select' : ''">
        <button
          @click="handleItemAnswer(item, index)"
          class="item-button"
          v-for="(item, index) in monitorAnswer"
          :key="index"
        >
          {{ item }}
        </button>
      </div>
      <div class="audio">
        <audio ref="refAudioCheck" style="display: 'none'">
          <source type="audio/ogg" />
        </audio>
        <div class="audio-box">
          <img
            v-show="isPlay"
            class="voulume-off"
            @click="playAudio"
            src="@/assets/icon/volume-off-solid.svg"
          />

          <img
            v-show="!isPlay"
            class="voulume-up"
            src="@/assets/icon/volume-up-solid.svg"
          />
        </div>

        <audio ref="refAudio">
          <source type="audio/ogg" />
        </audio>
      </div>
      <div v-if="!isResult" class="show-check">
        <div class="select-question">
          <button
            class="item-button"
            v-for="(item, index) in arrayAnswer"
            :key="index"
            @click="handleItemQuestion(index, item)"
            :class="monitorQuestion[index] ? 'hide' : ''"
          >
            {{ item }}
          </button>
        </div>

        <div class="check-button">
          <button @click="handleCheck">Check</button>
        </div>
      </div>

      <div
        v-show="isResult"
        class="show-answer"
        :class="currentcheck ? 'correct' : 'wrong'"
      >
        <div class="show-answer-content">
          <div class="show-answer-check">
            <div v-show="currentcheck" class="show-answer-check-correct">
              <img src="@/assets/icon/correct-circle-solid.svg" />
              <span>Correct</span>
            </div>
            <div v-show="!currentcheck" class="show-answer-check-wrong">
              <img src="@/assets/icon/wrong-circle-solid.svg" />
              <span>Wrong</span>
            </div>
          </div>
          <div class="show-answer-english">
            {{ listAnswer[current] }}
          </div>
          <div class="show-answer-vietnamese">
            {{ listTranslate[current] }}
          </div>
        </div>

        <div class="next-button" :class="currentcheck ? 'correct' : 'wrong'">
          <button @click="handleNext">Next</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import Loading from "./Loading.vue";
import { listnamequiz } from "../store/listnamequiz";
export default {
  components: { Loading },
  name: "main-quiz",
  data() {
    return {
      isCheck: true,
      begin: null,
      end: null,
      dataQuiz: [],
      isPlay: true,
      monitorQuestion: {},
      monitorAnswer: {},
      score: 0,
      current: 0,
      countMonitor: 0,
      isResult: false,
      currentcheck: false,
      nameQuiz: "",
      readyAudio: false,
    };
  },
  methods: {
    ...mapActions([
      "hideLoading",
      "showLoading",
      "hideMainQuiz",
      "showEndQuiz",
      "showResultQuiz",
    ]),
    handleCheck() {
      this.isCheck = !this.isCheck;
      let result = "";
      for (const [key, value] of Object.entries(this.monitorAnswer)) {
        key;
        result = result.concat(" " + value);
      }
      if (result.trim() === this.listAnswer[this.current].trim()) {
        this.$refs.refAudioCheck.src = `./audio/correct.mp3`;
        this.$refs.refAudioCheck.play();
        this.isResult = true;
        this.score = this.score + 1;
        this.currentcheck = true;
      } else {
        this.currentcheck = false;
        this.isResult = true;
        this.$refs.refAudioCheck.src = `./audio/wrong.mp3`;
        this.$refs.refAudioCheck.play();
      }
    },

    playAudio() {
      this.isPlay = false;
      this.$refs.refAudio.src = `./audio/${this.audio}.mp3`;
      this.$refs.refAudio.play();
      this.$refs.refAudio.onended = () => {
        this.isPlay = true;
      };
    },
    handleItemQuestion(index, item) {
      this.monitorQuestion[index] = item;
      this.monitorAnswer[this.countMonitor] = item;
      this.countMonitor++;
      this.$forceUpdate();
    },
    handleItemAnswer(item, index) {
      delete this.monitorAnswer[index];
      for (const [key, value] of Object.entries(this.monitorQuestion)) {
        if (value === item) {
          delete this.monitorQuestion[key];
          break;
        }
      }
      this.$forceUpdate();
    },
    handleNext() {
      this.$refs.refAudio.pause();
      this.$refs.refAudioCheck.pause();
      this.current = this.current + 1;
      this.audio = this.audio + 1;
      if (this.audio > this.end) {
        this.hideMainQuiz();
        this.showEndQuiz();
        this.showResultQuiz({
          nameQuiz: this.nameQuiz,
          score: `${this.score}/${this.end - this.begin + 1}`,
          email: JSON.parse(localStorage.user),
        });
      } else {
        this.playAudio();
        this.isResult = false;
        this.monitorQuestion = {};
        this.monitorAnswer = {};
      }
    },
    shuffle(a) {
      var j, x, i;
      for (i = a.length - 1; i > 0; i--) {
        j = Math.floor(Math.random() * (i + 1));
        x = a[i];
        a[i] = a[j];
        a[j] = x;
      }
      return a;
    },
    getData() {
      fetch(
        "https://script.google.com/macros/s/AKfycbxE5zoXMmbIoIRKIeyQyNcWRU20Z6oyWRitKEm8Jl9A6eHtb-XBzuVmJK5NZoZY3BglXg/exec?action=getData"
      )
        .then((response) => response.json())
        .then((data) => {
          this.dataQuiz = data;
          if (this.readyAudio) {
            this.hideLoading();
          }

          localStorage.dataQuiz = JSON.stringify(data);
        });
    },
    getnameQuiz() {
      this.nameQuiz = Object.keys(this.$route.query)[0];
      const flag = listnamequiz.find(
        (item) => `${item.title}_${item.name}` === this.nameQuiz
      );
      if (flag) {
        this.begin = flag.begin;
        this.end = flag.end;
        return true;
      } else {
        return false;
      }
    },
  },
  mounted() {
    if (this.getnameQuiz()) {
      this.audio = this.begin;
      this.$refs.refAudio.src = `./audio/${this.audio}.mp3`;
      this.$refs.refAudio.onloadedmetadata = () => {
        this.readyAudio = true;
      };
      if (localStorage.dataQuiz && this.readyAudio) {
        this.hideLoading();
        this.dataQuiz = JSON.parse(localStorage.dataQuiz);
      } else {
        this.showLoading();
        this.getData();
      }
    } else {
      this.showLoading();
    }
  },
  computed: {
    listAnswer() {
      return this.dataQuiz
        .filter((item) => item.id >= this.begin && item.id <= this.end)
        .map((item) => item.question);
    },
    listTranslate() {
      return this.dataQuiz
        .filter((item) => item.id >= this.begin && item.id <= this.end)
        .map((item) => item.translate);
    },
    arrayAnswer() {
      if (this.listAnswer[this.current]) {
        let splitAnswer = this.listAnswer[this.current].split(" ");
        let clonesplitAnswer = [...splitAnswer];
        while (splitAnswer.join("") === clonesplitAnswer.join("")) {
          this.shuffle(clonesplitAnswer);
        }
        return clonesplitAnswer;
      } else {
        return [""];
      }
    },

    ...mapGetters(["isLoading"]),
  },
};
</script>

<style lang="scss" scoped>
.prevent-select {
  pointer-events: none;
}
.main-quiz {
  width: 100%;
  height: 100vh;
  padding: 10px;
  display: flex;
  flex-direction: column;
  .progress {
    width: 100%;
    height: 5vh;
    display: flex;
    align-items: center;
    justify-content: center;
    &-bar {
      width: 100%;
      height: 2vh;
      background: #ccc;
      position: relative;
      border-radius: 10px;
    }
    &-percent {
      position: absolute;
      height: 100%;
      background: #f39c12;
      border-radius: 10px;
    }
  }

  .show-result {
    padding-top: 10px;
    width: 100%;
    flex: 1;
    display: flex;
    flex-direction: column;
  }

  .check-button,
  .next-button {
    button {
      background: #9b59b6;
      font-family: "Montserrat", sans-serif;
      font-size: 18px;
      font-weight: 600;
      color: white;
      border: 0;
      outline: none;
      width: 80%;
      padding: 15px 0;
      border-radius: 15px;
      cursor: pointer;
    }
  }
  .correct {
    button {
      background: #3498db;
    }
  }
  .wrong {
    button {
      background: #e74c3c !important;
    }
  }
  .show-check {
    flex: 1;
  }
  .audio {
    width: 100%;
    height: 10vh;

    display: flex;
    align-items: center;
    justify-content: center;
    &-box {
      width: 50px;
      height: 40px;

      display: flex;
      align-items: center;
    }

    img {
      margin-left: 15px;
      height: 40px;
      filter: invert(84%) sepia(46%) saturate(5554%) hue-rotate(353deg)
        brightness(98%) contrast(94%);
    }
  }
  .item-button {
    color: white;
    background: #8e44ad;
    font-family: "Montserrat", sans-serif;
    border: 0;
    font-weight: 500;
    outline: none;
    font-size: 18px;
    padding: 5px 10px;
    margin-right: 10px;
    cursor: pointer;
    margin-bottom: 10px;
  }

  .select-answer,
  .select-question {
    width: 100%;
    height: 35vh;
    text-align: left;
  }
  .false-result {
    background: #fff2f1;
  }
  .hide {
    background: #ccc;
    color: #ccc;
    pointer-events: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
  }

  .show-answer {
    flex: 1;

    &-content {
      width: 100%;
      height: 35vh;
    }
  }
  .correct {
    background: #e2f6ff;
  }
  .wrong {
    background: #fff2f1;
  }
  .show-answer-check {
    img {
      height: 30px;
    }
    &-correct,
    &-wrong {
      display: flex;
      align-items: center;
      span {
        font-size: 30px;
        font-weight: 600;
        padding-left: 10px;
      }
    }
    &-wrong {
      span {
        color: #ff5467;
      }
      img {
        filter: invert(44%) sepia(59%) saturate(709%) hue-rotate(305deg)
          brightness(99%) contrast(105%);
      }
    }
    &-correct {
      span {
        color: #3498db;
      }
      img {
        filter: invert(59%) sepia(18%) saturate(3662%) hue-rotate(177deg)
          brightness(88%) contrast(94%);
      }
    }
  }
  .show-answer-content {
    text-align: left;
    font-size: 20px;
    color: #302e4d;
    font-weight: 600;
    font-family: "Montserrat", sans-serif;
    padding-left: 10px;
    padding-top: 10px;
  }
  .show-answer {
    &-english,
    &-vietnamese {
      margin: 10px 0;
    }
  }
}
</style>
