<template>
  <div class="menu">
    <div class="btn-install">
      <button v-if="deferredPrompt" @click="install">
        <span> install</span>
      </button>
    </div>
    <div class="menu-title">Quiz Toeic Part 1</div>

    <div class="list-quiz">
      <router-link
        :to="`/quiz?${item.title}_${item.name}`"
        class="item-quiz"
        :class="item.name"
        v-for="item in listQuiz"
        :key="item.id"
      >
        <div class="item-quiz-title">{{ item.title.toUpperCase() }}</div>
        <div class="item-quiz-name">{{ item.name.toUpperCase() }}</div>
      </router-link>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { listnamequiz } from "../store/listnamequiz";
export default {
  name: "MenuView",
  components: {},
  data() {
    return {
      deferredPrompt: null,
      listQuiz: listnamequiz,
    };
  },
  created() {
    window.addEventListener("beforeinstallprompt", (e) => {
      e.preventDefault();
      this.deferredPrompt = e;
    });
    window.addEventListener("appinstalled", () => {
      this.deferredPrompt = null;
    });
  },
  methods: {
    async install() {
      this.deferredPrompt.prompt();
    },
  },
};
</script>
