<template>
  <div class="register">
    <div class="submit">
      <input
        v-model="email"
        type="text"
        placeholder="Enter your email"
        @focus="hanldeFoucs"
        ref="refInput"
      />
      <h4 v-if="error">Enter your email again!</h4>
      <button @click="handleSubmit">Submit</button>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "register-com",
  data() {
    return {
      email: "",
      error: false,
    };
  },
  methods: {
    ...mapActions(["hideRegister", "showMainQuiz"]),
    handleSubmit() {
      if (!this.validateEmail(this.email)) {
        this.error = true;
      } else {
        this.hideRegister();
        this.showMainQuiz();
        localStorage.user = JSON.stringify(this.email);
      }
    },
    hanldeFoucs() {
      this.error = false;
    },
    validateEmail(email) {
      let mailformat =
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      if (email.match(mailformat)) {
        return true;
      }
      return false;
    },
  },
  computed: {},
  mounted() {
    this.$refs.refInput.focus();
  },
};
</script>

<style lang="scss" scoped>
.register {
  width: 100%;
  height: 100%;

  .submit {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    button {
      cursor: pointer;
      width: 70%;
      padding: 15px 0;
      color: white;
      background: #3498db;
      border: 0;
      outline: none;
      border-radius: 10px;
      font-size: 20px;
      font-family: "Montserrat", sans-serif;
      font-weight: 600;
    }
    h4 {
      color: #c0392b;
    }
    input {
      -webkit-user-select: text;
      -moz-user-select: text;
      -ms-user-select: text;
      user-select: text;
      width: 70%;
      font-size: 20px;
      font-family: "Montserrat", sans-serif;
      font-weight: 600;
      padding: 10px 15px;
      outline: 2px solid black;
      border: 0;
      border-radius: 10px;
      &:focus {
        outline-color: #3498db;
      }
    }
  }
}
</style>
