<template>
  <div class="end-quiz">
    <div class="end-quiz-title">{{ resultQuiz.nameQuiz }}</div>
    <div class="end-quiz-score">Score: {{ resultQuiz.score }}</div>
    <div class="end-quiz-menu common-button">
      <router-link to="/">
        <button>Menu</button>
      </router-link>
    </div>
    <div class="end-quiz-youtube common-button">
      <a href="https://www.youtube.com/channel/UCfGWPz1rB5VvngiMZMbojcA">
        <button>Subcribe Youtube</button>
      </a>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "end-quiz",
  methods: {
    async sendGoogleSheet() {
      const scriptURL =
        "https://script.google.com/macros/s/AKfycbyQCZSJe-cyQS_pwJitp9B0Q_5hXq2Pe8zyRmjDM7ZBtLIxl94itJr5ttJ5xbIzi17m/exec";

      let formData = new FormData();
      formData.append("Email", this.resultQuiz.email);
      formData.append("Quiz", this.resultQuiz.nameQuiz);
      formData.append("Score", this.resultQuiz.score);

      try {
        await fetch(scriptURL, { method: "POST", body: formData });
      } catch (error) {
        console.log(error);
      }
    },
  },
  computed: {
    ...mapGetters(["resultQuiz"]),
  },
  mounted() {
    this.sendGoogleSheet();
  },
};
</script>

<style lang="scss">
.end-quiz {
  width: 100%;
  height: 100vh;
  background: white;
  font-family: "Montserrat", sans-serif;
  padding: 20px;
  font-size: 20px;
  &-title {
    margin-top: 50px;
    color: #ff0000;

    font-weight: 600;
  }
  &-score {
    color: #2c3e50;
    font-weight: 600;
    font-size: 25px;
    margin-top: 20px;
  }

  .common-button {
    button {
      cursor: pointer;
      border: 0;
      outline: none;
      color: white;
      background: #8e44ad;
      font-size: 20px;
      font-family: "Montserrat", sans-serif;
      font-weight: 600;
      width: 80%;
      padding: 15px 0;
      border-radius: 10px;
    }
  }
  &-menu {
    margin-top: 25px;
  }
  &-youtube {
    margin-top: 25px;
    button {
      background: #ff0000 !important;
    }
  }
}
</style>
