<template>
  <div class="quiz">
    <Register v-if="isRegister"></Register>
    <MainQuiz v-if="isMainQuiz"></MainQuiz>
    <EndQuiz v-if="isEndQuiz"></EndQuiz>
  </div>
</template>

<script>
import Register from "@/components/Register.vue";
import { mapActions, mapGetters } from "vuex";
import MainQuiz from "@/components/MainQuiz.vue";
import EndQuiz from "@/components/EndQuiz.vue";

export default {
  name: "quiz-comp",
  components: { Register, MainQuiz, EndQuiz },
  methods: {
    ...mapActions(["hideRegister", "showMainQuiz"]),
  },
  mounted() {
    if (localStorage.user) {
      this.hideRegister();
      this.showMainQuiz();
    }
  },
  computed: {
    ...mapGetters(["isRegister", "isMainQuiz", "isEndQuiz"]),
  },
};
</script>

<style lang="scss">
.quiz {
  height: 100vh;
  width: 100%;
  max-width: 390px;
  max-height: 844px;
  background: white;
  margin: 0 auto;
}
//Responsive
@mixin maxWidth($width) {
  @media screen and (max-width: $width) {
    @content;
  }
}
@mixin minWidth($width) {
  @media screen and (min-width: $width) {
    @content;
  }
}
@include maxWidth(767px) {
}
@include maxWidth(426px) {
}
</style>
