import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isRegister: true,
    isLoading: false,
    isMainQuiz: false,
    isEndQuiz: false,
    resultQuiz: { nameQuiz: "", score: "", email: "" },
  },
  getters: {
    isRegister: (state) => state.isRegister,
    isMainQuiz: (state) => state.isMainQuiz,
    isLoading: (state) => state.isLoading,
    isEndQuiz: (state) => state.isEndQuiz,
    resultQuiz: (state) => state.resultQuiz,
  },
  mutations: {
    setRegister(state, value) {
      state.isRegister = value;
    },
    setMainQuiz(state, value) {
      state.isMainQuiz = value;
    },
    setLoading(state, value) {
      state.isLoading = value;
    },
    setEndQuiz(state, value) {
      state.isEndQuiz = value;
    },
    setResultQuiz(state, value) {
      state.resultQuiz = value;
    },
  },
  actions: {
    hideRegister(context) {
      context.commit("setRegister", false);
    },
    showMainQuiz(context) {
      context.commit("setMainQuiz", true);
    },
    hideMainQuiz(context) {
      context.commit("setMainQuiz", false);
    },

    showLoading(context) {
      context.commit("setLoading", true);
    },
    hideLoading(context) {
      context.commit("setLoading", false);
    },
    showEndQuiz(context) {
      context.commit("setEndQuiz", true);
    },
    showResultQuiz(context, value) {
      context.commit("setResultQuiz", value);
    },
  },
  modules: {},
});
